import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { RootState } from ".";

export interface Main {
    appIsLoaded: boolean;
    // onboardingComplete: boolean;
    currentModal: string | null;
    days: Day[];
    isLogin: boolean;
    userEmail: string;
}

export enum DayType {
    CHOCOLATE = "chocolate",
    NUT = "nut",
    WAFFLE = "waffle",
    MILK = "milk",
    CARAMEL = "caramel",
}

export enum GameType {
    TEST = "test",
    CHECKLIST = "checklist",
    PHRAZLE = "phrazle",
    QUIZ = "quiz",
    SWIPER_CHOICE = "swiper_choice",
    CARD_MATCH = "card_match",
    NUMBER_INPUT = "number_input",
    CHOOSE_PIC = "choose_pic",
    PUZZLE = "puzzle",
    PIC_COMMENT = "pic_comment",
    CATCH_ITEM = "catch_item",
    CARDS_ORDER = "cards_order",
    PUSH_BUTTON = "push_button",
    CHOOSE_COMMENT = "choose_comment",
    CHOOSE_INSPIRE = "choose_inspire",
    UPLOAD_PIC = "upload_pic",
}

export interface Day {
    id: number;
    pic?: string;
    name: string;
    active: boolean;
    done: boolean;
    penalty: boolean;
    type: DayType;
    icon?: string;
    time: string;
    games: Game[];
}

export interface Game {
    id: number;
    title: string;
    pic: string;
    picHeader: string;
    available: boolean;
    done: boolean;
    type: GameType;
    test?: TestQuestion[];
    complete: {
        pic?: string;
        title: string;
        lose?: string;
        text: string;
    };
    description: string;
}

export interface TestQuestion {
    id: number;
    question: string;
    answers: {
        id: number;
        text: string;
    }[];
}

export interface ApiGame {
    game_title: string;
    day_number: string;
    game_completed: boolean;
    complete_date: string;
}

export interface ApiDay {
    day_number: string;
    day_title: string;
    day_status: string;
    day_open_date: string;
    day_complete_time: string;
    day_games: ApiGame[];
}

